.view {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.viewContent {
  flex: 1 0 auto;
  padding: 0 0 0;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.viewContent:after {
  content: '/00a0';
  display: block;
  margin-top: 0;
  height: 0;
  visibility: hidden;
}
